import { User } from './user';
import { Appointment } from './appointment';
export class Offer {
    id: number;
    offerStatus: string;
    sender_Appointment: Appointment;
    receiver_Appointment: Appointment;
    sender: User;
    receiver: User;

    constructor(id: number,
         offerStatus: string,
         sender_Appointment: Appointment,
         receiver_Appointment: Appointment,
         sender: User,
         receiver: User) {
        this.id = id;
        this.offerStatus = offerStatus;
        this.sender_Appointment = sender_Appointment;
        this.receiver_Appointment = receiver_Appointment;
        this.sender = sender;
        this.receiver = receiver;
    }
}
