import {HttpClient} from '@angular/common/http';
import {Injectable} from '@angular/core';
import { Appointment } from 'src/app/shared/models/appointment';
import { SessionUserService } from '../session-user-service/session-user.service';
import { AppointmentTypes } from 'src/app/shared/enums/static-strings';
import { ReplaySubject } from 'rxjs/internal/ReplaySubject';
import { BehaviorSubject } from 'rxjs/internal/BehaviorSubject';
import { Observable } from 'rxjs/internal/Observable';
import { map } from 'rxjs/internal/operators/map';


const SERVER_URL = 'https://bfk-portal.herokuapp.com/api/appointment';
@Injectable({providedIn: 'root'})
export class AppointmentService {

  appointments: Appointment[] = [];
  appointmentToDelete = new ReplaySubject<Appointment>();
  _allAppointments = new BehaviorSubject<Appointment[]>([]);
  _appointmentToAdd = new ReplaySubject<Appointment>();
  appointmentToUpdate = new ReplaySubject<Appointment>();
  _myShifts = new ReplaySubject<Appointment[]>();
  _shiftToMarket = new ReplaySubject<any>();
  _freeShiftToTake = new ReplaySubject<number>();

  constructor(private httpClient: HttpClient,
    private sessionUser: SessionUserService) {
  }


 getAppointments() {
   console.log('Get Appointments');
      return this
      .httpClient
      .get<any>(SERVER_URL + '/all').pipe(map(response => {
        this.appointments = [];
        response.forEach(appointment => {
          this.appointments.push(new Appointment(appointment.id, appointment.title, appointment.description,
            appointment.from, appointment.to,
            appointment.participations, appointment.maxParticipants, appointment.deadline, appointment.showParticipants,
            appointment.owner, appointment.type, appointment.isVisible, appointment.areParticipantsOrganisations));
        });
        console.log(this.appointments);
        this._allAppointments.next(this.appointments);
        return true;
        }));
  }
/**
 *Get the Types of an Appointment
 *
 * @returns {Observable < string[] >}
 * @memberof AppointmentService
 */
getTypes(): Observable < string[] > {
    return this.httpClient.get < string[] > (SERVER_URL + '/types');
  }
  /**
   *Add an Appointment to Database
   *
   * @param {Appointment} appointment
   * @returns {Observable < any >}
   * @memberof AppointmentService
   */
  addAppointment(appointment: Appointment): Observable < any > {

    return this
      .httpClient
      .post(SERVER_URL + '/add', {
        title: appointment.title,
        description: appointment.description,
        from: appointment.from,
        to: appointment.to,
        type: appointment.type,
        maxParticipants: appointment.maxParticipants,
        showParticipants: appointment.showParticipants,
        deadline: appointment.deadline,
        isVisible: appointment.isVisible,
        participations: this.getIdFromParticipants(appointment),
        owner: appointment.owner.id,
        areParticipantsOrganisations: appointment.areParticipantsOrganisations
      })
      .pipe(map((response: any): {success: boolean, id: number} => {
        console.log(response);
          appointment.id = response.id;
          console.log(appointment);
          this.appointments = [...this.appointments, appointment];
          this._allAppointments.next(this.appointments);
        return {success: true, id: appointment.id};
      }));
  }
  updateAppointment(appointment: Appointment) {
    console.log(appointment);
    return this
      .httpClient
      .post(SERVER_URL + '/update', {
        id : appointment.id,
        title: appointment.title,
        description: appointment.description,
        from: appointment.from,
        to: appointment.to,
        type: appointment.type,
        maxParticipants: appointment.maxParticipants,
        showParticipants: appointment.showParticipants,
        deadline: appointment.deadline,
        isVisible: appointment.isVisible,
        participations: this.getIdFromParticipants(appointment),
        owner: appointment.owner.id,
        areParticipantsOrganisations: appointment.areParticipantsOrganisations
      })
      .pipe(map((response: any) => {
        return true;
      }));
  }
  deleteAppointment(appointment: Appointment) {
    return this.httpClient.get(SERVER_URL + '/delete/' + appointment.id)
    .pipe(map(response => {
      this.appointments.splice(this.appointments.indexOf(appointment));
      this._allAppointments.next(this.appointments);
      return true;
    }));
  }
  participateInAppointment(appointmentId, participantId) {
    return this.httpClient.post(SERVER_URL + '/participate', {
      appointmentId: appointmentId,
      participantId : participantId
    }).pipe(map(response => {
      return true;
    }));
  }
  sendShiftToMarket(appointmentId) {
    return this.httpClient.get(SERVER_URL + '/offer/' + appointmentId).pipe(map(response => {
      return true;
    }));
  }
  takeFreeShift(appointmentId) {
    return this.httpClient.get(SERVER_URL + '/take/' + appointmentId).pipe(map(response => {
      return true;
    }));
  }
  unparticipateInAppointment(appointmentId, participantId) {
    return this.httpClient.post(SERVER_URL + '/unparticipate', {
      appointmentId: appointmentId,
      participantId : participantId
    }).pipe(map(response => {
      console.log(response);
      return true;
    }));
  }
  getIdFromParticipants(appointment: Appointment): Array<number> {
    const participantsNumbers = [];
    if (appointment.type === 'Dienst') {
      participantsNumbers.push(appointment.owner.id);
    } else {
      appointment.participations.forEach(element => {
        participantsNumbers.push(element.id);
      });
    }
    return participantsNumbers;
  }
  checkIfShiftIsMine(appointment: Appointment): boolean {
    if (appointment.owner.email === this.sessionUser.getUser().email) {
      return true;
    }
    return false;
  }
  getOwnShifts(appointments: Appointment[]) {
    let filteredAppointments: Appointment[];
    filteredAppointments = appointments.filter(appointment => appointment.type === AppointmentTypes[AppointmentTypes.Dienst]
      || appointment.type === AppointmentTypes[AppointmentTypes.MarktplatzDienst]);
      filteredAppointments = filteredAppointments.filter(appointment => appointment.to >= new Date()
      && appointment.owner.email === this.sessionUser.getUser().email);
      this._myShifts.next(filteredAppointments);
  }
  filterAppointments(appointments: Appointment[], tabIndex: number):
  {partedAppointments: Array<Appointment[]>, filteredAppointments: Array<Appointment>} {
    let filteredAppointments: Appointment[];
    if (tabIndex === 1) {
      filteredAppointments = appointments.filter(appointment => appointment.type !== AppointmentTypes[AppointmentTypes.Dienst]
        && appointment.type !== AppointmentTypes[AppointmentTypes.MarktplatzDienst]
        && appointment.type !== AppointmentTypes[AppointmentTypes.FreierDienst]);
        filteredAppointments = filteredAppointments.filter(appointment => appointment.to >= new Date());
    } else if (tabIndex === 2) {
      console.log(appointments);
      filteredAppointments = appointments.filter(appointment => appointment.type === AppointmentTypes[AppointmentTypes.Dienst]
        || appointment.type === AppointmentTypes[AppointmentTypes.MarktplatzDienst]
        || appointment.type === AppointmentTypes[AppointmentTypes.FreierDienst]);
        console.log(filteredAppointments);
        filteredAppointments = filteredAppointments.filter(appointment => appointment.to >= new Date());
    } else if (tabIndex === 3) {
      filteredAppointments = appointments.filter(appointment => appointment.to <= new Date());
    }
    filteredAppointments.sort((a, b) => a.from.getTime() - b.from.getTime());
    return {partedAppointments: Appointment.groupAppointments(filteredAppointments), filteredAppointments: filteredAppointments};
  }

}
