import * as moment from 'moment';
import * as _ from 'lodash';
import {User} from './user';
import {AppointmentTypes} from '../enums/static-strings';

export class Appointment {
    id = 0;
    type = '';
    title = '';
    description = '';
    fromDateTime: Date;
    from: Date;
    to: Date;
    moreDays: boolean;
    participations: Array < any > = [];
    maxParticipants = 0;
    showParticipants = false;
    deadline: Date | null | undefined;
    owner: User;
    isVisible: boolean;
    areParticipantsOrganisations: boolean;
    // tslint:disable-next-line:max-line-length
    constructor(id: number,
        title: string,
        description: string,
        from: Date,
        to: Date,
        participations: Array < any >,
        maxParticipants: number,
        deadLine: Date,
        showParticipants: boolean,
        owner: User, type: string,
        isVisible: boolean,
        areParticipantsOrganisations: boolean) {
        this.id = id;
        this.title = title;
        this.description = description;
        this.fromDateTime = moment
            .utc(from)
            .toDate();
        this.from = moment
            .utc(from)
            .toDate();
        this.to = moment
            .utc(to)
            .toDate();
        this
            .from
            .getDate() !== this
            .to
            .getDate()
            ? this.moreDays = true
            : this.moreDays = false;

        this.participations = participations;
        if (deadLine !== null) {
            this.deadline = moment
                .utc(deadLine)
                .toDate();
        } else {
            this.deadline = deadLine;
        }

        this.showParticipants = showParticipants;
        this.maxParticipants = maxParticipants;
        this.owner = owner;
        this.type = type;
        this.isVisible = isVisible;
        this.areParticipantsOrganisations = areParticipantsOrganisations;
    }
    static checkColor(appointment: Appointment) {
        if (appointment.type === AppointmentTypes[AppointmentTypes.Dienst] ||
            appointment.type === AppointmentTypes[AppointmentTypes.FreierDienst] ||
            appointment.type === AppointmentTypes[AppointmentTypes.MarktplatzDienst]) {
            return ' #ad2121';
        } else if (appointment.type === AppointmentTypes[AppointmentTypes.Vollversammlung]) {
            return '#e3bc08';
        } else if (appointment.type === AppointmentTypes[AppointmentTypes.Bewerb]) {
            return '#1e90ff';
        } else if (appointment.type === AppointmentTypes[AppointmentTypes.Weiterbildung]) {
            return '#607d8b';
        } else if (appointment.type === AppointmentTypes[AppointmentTypes.Lehrgang]) {
            return '#4caf50';
        }
    }
    static groupAppointments(appointments: Array < Appointment >): Array < Appointment[] > {
        const partedAppointments: Array < Appointment[] > = [];
        for (const key in _.groupBy(appointments, (b: Appointment) => {
            return moment(b.fromDateTime)
                .startOf('date')
                .format('DD.MM.YYYY');
        })) {
            if (_.groupBy(appointments, (b: Appointment) => {
                return moment(b.fromDateTime)
                    .startOf('date')
                    .format('DD.MM.YYYY');
            }).hasOwnProperty(key)) {
                const value = _.groupBy(appointments, (b: Appointment) => {
                    return moment(b.fromDateTime)
                        .startOf('date')
                        .format('DD.MM.YYYY');
                })[key];
                partedAppointments.push(value);
            }
        }
        return partedAppointments;
    }
    /**
     *
     */
}
