import {Injectable} from '@angular/core';

@Injectable({providedIn: 'root'})
export class NavbarButtonsService {
  isCalendarView: boolean;
  isDocumentView: boolean;
  isListView: boolean;
  isListHistoryView: boolean;
  isMarketPlaceView: boolean;
  isOfferView: boolean;
  isOverviewView: boolean;
  isListUserManagementView: boolean;
  isDetailsView: boolean;
  isAddUserManagementView: boolean;

  constructor() {}

  changeToCalendarView(): void {
    this.isListUserManagementView = false;
    this.isAddUserManagementView = false;
    this.isListView = false;
    this.isCalendarView = true;
    this.isDocumentView = false;
    this.isMarketPlaceView = false;
    this.isOverviewView = false;
    this.isDetailsView = false;
  }
  changeToDocuments(): void {
    this.isListUserManagementView = false;
    this.isAddUserManagementView = false;
    this.isListView = false;
    this.isCalendarView = false;
    this.isDocumentView = true;
    this.isMarketPlaceView = false;
    this.isOverviewView = false;
    this.isDetailsView = false;
  }
  changeToListView(): void {
    this.isListUserManagementView = false;
    this.isAddUserManagementView = false;
    this.isListView = true;
    this.isCalendarView = false;
    this.isDocumentView = false;
    this.isMarketPlaceView = false;
    this.isOverviewView = false;
    this.isDetailsView = false;
  }
  changeToMarketPlaceView(): void {
    this.isListView = false;
    this.isCalendarView = false;
    this.isDocumentView = false;
    this.isMarketPlaceView = true;
    this.isOverviewView = false;
    this.isListUserManagementView = false;
    this.isAddUserManagementView = false;
    this.isDetailsView = false;
  }
  changeToListUserManagementView(): void {
    this.isListView = false;
    this.isCalendarView = false;
    this.isDocumentView = false;
    this.isMarketPlaceView = false;
    this.isOverviewView = false;
    this.isListUserManagementView = true;
    this.isAddUserManagementView = false;
    this.isDetailsView = false;
  }
  changeToAddUserManagementView(): void {
    this.isListView = false;
    this.isCalendarView = false;
    this.isDocumentView = false;
    this.isMarketPlaceView = false;
    this.isOverviewView = false;
    this.isListUserManagementView = false;
    this.isAddUserManagementView = true;
    this.isDetailsView = false;
  }
  changeToOverviewView(): void {
    this.isListUserManagementView = false;
    this.isAddUserManagementView = false;
    this.isListView = false;
    this.isCalendarView = false;
    this.isDocumentView = false;
    this.isMarketPlaceView = false;
    this.isOverviewView = true;
    this.isDetailsView = false;
  }
  changeToDetailsView(): void {
    this.isListUserManagementView = false;
    this.isAddUserManagementView = false;
    this.isListView = false;
    this.isCalendarView = false;
    this.isDocumentView = false;
    this.isMarketPlaceView = false;
    this.isOverviewView = false;
    this.isDetailsView = true;
  }
}
