import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Offer } from 'src/app/shared/models/offer';
import { ReplaySubject } from 'rxjs/internal/ReplaySubject';
import { Observable } from 'rxjs/internal/Observable';
import { map } from 'rxjs/internal/operators/map';

const SERVER_URL = 'https://bfk-portal.herokuapp.com/api/offer';
@Injectable({
  providedIn: 'root'
})
export class OfferService {

  _offerToAdd = new ReplaySubject<Offer>();
  _replyToOffer = new ReplaySubject<any>();
  _sentOffers = new ReplaySubject<Offer[]>();
  _receivedOffers = new ReplaySubject<Offer[]>();

  constructor(private httpClient: HttpClient) { }

  getOffers(): Observable<any> {
    return this.httpClient.get(SERVER_URL + '/all');
  }
  addOffer(offer: Offer): Observable<any> {
    return this
      .httpClient
      .post(SERVER_URL + '/add', {
        status: 'pending',
        receiverId: offer.receiver.id,
        receiverAppointmentId: offer.receiver_Appointment.id,
        senderId: offer.sender.id,
        senderAppointmentId: offer.sender_Appointment.id
      })
      .pipe(map((response: any) => {
        console.log(response);
        return response;
      }));
  }
  replyOffer(offerId: number, status: string) {
    return this.httpClient.post(SERVER_URL + '/reply', {
      offerId: offerId,
      status: status
    }).pipe(map(response => {
      return true;
    }));
  }
}
