import {HttpClient} from '@angular/common/http';
import {Injectable} from '@angular/core';
import { User } from 'src/app/shared/models/user';
import { Organisation } from 'src/app/shared/models/organisation';
import { ReplaySubject } from 'rxjs/internal/ReplaySubject';
import { Observable } from 'rxjs/internal/Observable';
import { map } from 'rxjs/internal/operators/map';

const SERVER_URL = 'https://bfk-portal.herokuapp.com/api/user';
@Injectable({providedIn: 'root'})

export class UserService {

  _allUsers = new ReplaySubject<User[]>();
  _userToUpdate = new ReplaySubject<User>();
  _userToAdd = new ReplaySubject<User>();
  _userToDelete = new ReplaySubject<User>();

  constructor(private httpClient: HttpClient) {}

  getUsers(): Observable < any > {
    return this
      .httpClient
      .get(SERVER_URL + '/all');
  }
  getRoles(): Observable<any> {
    return this.httpClient.get<any>(SERVER_URL + '/roles');
  }

  addUser(user: User): Observable<any> {
    const orgs = this.getNamesOfOrgs(user.memberships);
    return this.httpClient.post(SERVER_URL + '/add', {
      firstname: user.firstname,
      lastname: user.lastname,
      email: user.email,
      organisations: orgs,
      roles: user.entitlements,
    })
    .pipe(map(response => {
      return response;
      }));
  }
  deleteUser(user: User): Observable<any> {
    return this.httpClient.get(SERVER_URL + '/delete/' + user.id)
    .pipe(map(response => {
      return true;
      }));
  }
  updateUser(user: User): Observable<any> {
    const orgs = this.getNamesOfOrgs(user.memberships);
    return this.httpClient.post(SERVER_URL + '/update', {
      id: user.id,
      firstname: user.firstname,
      lastname: user.lastname,
      email: user.email,
      organisations: orgs,
      roles: user.entitlements,
    })
    .pipe(map(response => {
      return true;
      }));
  }

  getNamesOfOrgs(orgs: Organisation[]): string[] {
    const stringOrgs: Array<string> = [];
    orgs.forEach(org => {
      stringOrgs.push(org.name);
    });
    return stringOrgs;
  }
}
