import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Organisation } from 'src/app/shared/models/organisation';
import { Observable } from 'rxjs/internal/Observable';
import { ReplaySubject } from 'rxjs/internal/ReplaySubject';
import { map } from 'rxjs/internal/operators/map';


const SERVER_URL = 'https://bfk-portal.herokuapp.com/api/organisation';
@Injectable({
  providedIn: 'root'
})
export class OrganisationService {
  _allOrganisations = new ReplaySubject<Organisation[]>();

  constructor(private httpClient: HttpClient) { }

  getOrganisations(): Observable<boolean> {
    return this.httpClient.get<Organisation[]>(SERVER_URL + '/all').pipe(map(response => {
      this._allOrganisations.next(response);
      return true;
    }));
  }
}
