import { Injectable } from '@angular/core';
import { AngularFireMessaging } from '@angular/fire/messaging';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { FcmNotification } from 'src/app/shared/models/fcmNotification';
import { ToastService } from 'ng-uikit-pro-standard';


const SERVER_URL = 'https://bfk-portal.herokuapp.com/api/user/';
// tslint:disable-next-line:max-line-length
const SERVER_KEY = 'AAAALzYmV90:APA91bFCrT6Ll9cf_2U3EGre_b_F8Vg611TXulIjW3_bXVPL2lCDK0_inXosBwxq5S1xusbpnqIiGAnYtdO8XQ-lsDAmFe7LvEK19WsxaioEVQPFj-Wad_tJeoNP1cnkB2q7Sd3HULsr';
@Injectable({
  providedIn: 'root'
})
export class MessagingService {
  allUnReadMessages: Array<FcmNotification> = [];

  constructor(private angularFireMessaging: AngularFireMessaging,
    private httpClient: HttpClient,
    private toast: ToastService) {
    this.angularFireMessaging.messaging.subscribe((_messaging) => {
      _messaging.onMessage = _messaging.onMessage.bind(_messaging);
      _messaging.onTokenRefresh = _messaging.onTokenRefresh.bind(_messaging);
    });
   }

   /**
   * update token in firebase database
   *
   * @param userId userId as a key
   * @param token token as a value
   */
  updateToken(userId, token) {
    this.httpClient.post(SERVER_URL + 'settoken', {
      userId: userId,
      token: token
    });
  }
  getToken(userId) {
    return this.httpClient.get(SERVER_URL + 'gettoken/' + userId);
  }

  /**
   * request permission for notification from firebase cloud messaging
   *
   * @param userId userId
   */
  getPermission(userId) {
    this.angularFireMessaging.requestPermission
.subscribe(() => {
      this.angularFireMessaging.requestToken.subscribe(
        (token) => {
          console.log(token);
          this.updateToken(userId, token);
          this.toast.info('Du erhälst Notifications');
        },
        (err) => {
          console.error('Unable to get permission to notify.', err);
          this.toast.info('Du erhälst keine Notifications');
        }
      );
    }, (error) => console.log(error));
  }

  removeMessage(index: number): any {
    const copyAllUnreadMessages = this.allUnReadMessages;
    copyAllUnreadMessages.splice(index, 1);
    this.allUnReadMessages = copyAllUnreadMessages;
    localStorage.setItem('allUnreadMessages', JSON.stringify(this.allUnReadMessages));
  }
  sendNotification(to: string, fcmNotification: FcmNotification) {
    const httpheaders = new HttpHeaders();
    httpheaders.append('Authorization', 'key=' + SERVER_KEY);
    httpheaders.append('Content-Type', 'application/json');
    this.httpClient.post('https://fcm.googleapis.com/fcm/send', {
      notification: fcmNotification,
      to: to
    }, {headers: httpheaders});
  }
  getMessages(): void {
      this.allUnReadMessages = JSON.parse(localStorage.getItem('allUnreadMessages')) || [];
  }

  /**
   * hook method when new notification received in foreground
   */
  receiveMessage() {
    this.angularFireMessaging.messages.subscribe(
      (payload: any) => {
        const newNotification = new FcmNotification();
        newNotification.body = payload.notification.body;
        newNotification.title = payload.notification.title;
        newNotification.click_action = payload.notification.click_action;
        const copyAllUnreadMessages = this.allUnReadMessages || [];
        copyAllUnreadMessages.push(newNotification);
        this.allUnReadMessages = copyAllUnreadMessages;
        localStorage.setItem('allUnreadMessages', JSON.stringify(this.allUnReadMessages));
      });
  }
}
