import {ToastService} from 'ng-uikit-pro-standard';
import {Injectable} from '@angular/core';
import {Router, CanActivate, NavigationStart} from '@angular/router';
import { RolesService } from '../services/roles-service/roles.service';

@Injectable({
    providedIn: 'root'
  })
export class AuthGuard implements CanActivate {

    constructor(private router: Router, private rolesService: RolesService, private toast: ToastService) {}

    canActivate() {
        if (localStorage.getItem('checked') === 'true') {
            if (localStorage.getItem('currentUser')) {
                return true;

            }
            this
                .router
                .navigate(['/login']);

            return false;

        }
        if (sessionStorage.getItem('currentUser')) {
            return true;

        }
        this
            .router
            .navigate(['/login']);

        return false;

    }
}
