import { Injectable } from '@angular/core';
import { CanActivate, ActivatedRouteSnapshot, RouterStateSnapshot, Router } from '@angular/router';
import { RolesService } from '../services/roles-service/roles.service';
import { Observable } from 'rxjs/internal/Observable';
import { ToastService } from 'ng-uikit-pro-standard';

@Injectable({
  providedIn: 'root'
})
export class UserGuard implements CanActivate {
  constructor(private router: Router, private rolesService: RolesService, private toast: ToastService) {}
  canActivate(
    next: ActivatedRouteSnapshot,
    state: RouterStateSnapshot): Observable<boolean> | Promise<boolean> | boolean {
      this.rolesService.setRoles();
      if (this.rolesService.isAdminBfk || this.rolesService.isAdminBwst) {
        return true;
      }
    this.toast.error('Route verboten');
    this.router.navigate(['home']);
    return false;
  }
}
