import {Organisation} from './organisation';

export class User {
    id = 0;
    firstname = '';
    lastname = '';
    email = '';
    entitlements: Array < string > = [];
    memberships: Array < Organisation > = [];
    isDeleted = false;
    telephone = '';

    static checkparticipations(user) {
        if (!!user.email) {
            return true;
        } else {
            return false;
        }
    }
}
