import {BrowserModule} from '@angular/platform-browser';
import {NgModule, NO_ERRORS_SCHEMA} from '@angular/core';
import {MDBSpinningPreloader,
  ToastModule,
  NavbarModule,
  WavesModule,
  ButtonsModule,
  SidenavModule,
  AccordionModule,
  DropdownModule,
  BadgeModule,
  PopoverModule} from 'ng-uikit-pro-standard';
import {HttpClientModule, HTTP_INTERCEPTORS} from '@angular/common/http';
import LocaleDe from '@angular/common/locales/de-AT';

import {AppRoutingModule} from './app_routing.module';
import {AppComponent} from './app.component';
import {FormsModule, ReactiveFormsModule} from '@angular/forms';
import {BrowserAnimationsModule} from '@angular/platform-browser/animations';
import {CommonModule, registerLocaleData} from '@angular/common';
import { ServiceWorkerModule } from '@angular/service-worker';
import { environment } from '../environments/environment';
import { AuthInterceptorService } from './core/interceptors/auth-interceptor/auth-interceptor.service';
import { NavbarComponent } from './navbar/navbar.component';
import { LoginModule } from './modules/login/login.module';
import { NotFoundModule } from './modules/not-found/not-found.module';
import { ForgotPWModule } from './modules/forgot-pw/forgot-pw.module';
import { ErrorInterceptorService } from './core/interceptors/error-interceptor/error-interceptor.service';
import { AngularFireMessagingModule } from '@angular/fire/messaging';
import { AngularFireModule } from '@angular/fire';
import {
  HammerGestureConfig,
  HAMMER_GESTURE_CONFIG
} from '@angular/platform-browser';
import * as Hammer from 'hammerjs';

registerLocaleData(LocaleDe);

export class MyHammerConfig extends HammerGestureConfig {
  buildHammer(element: HTMLElement) {
    const mc = new Hammer(element, {
      touchAction: 'pan-y'
    });

    return mc;
  }
}

@NgModule({
  declarations: [
    AppComponent,
    NavbarComponent,
  ],
  imports: [
    BrowserModule,
    BrowserAnimationsModule,
    HttpClientModule,
    AppRoutingModule,
    FormsModule,
    ReactiveFormsModule,
    CommonModule,
    LoginModule,
    NotFoundModule,
    ForgotPWModule,
    AngularFireMessagingModule,
    AngularFireModule.initializeApp(environment.firebase),
    ToastModule.forRoot(),
    NavbarModule,
    WavesModule,
    SidenavModule,
    AccordionModule,
    ButtonsModule,
    DropdownModule,
    BadgeModule,
    PopoverModule,
    ServiceWorkerModule.register('ngsw-worker.js', { enabled: environment.production })
  ],
  schemas: [NO_ERRORS_SCHEMA],
  providers: [
    MDBSpinningPreloader,
    {
      provide: HTTP_INTERCEPTORS,
      useClass: AuthInterceptorService,
      multi: true
    },
    {
      provide: HTTP_INTERCEPTORS,
      useClass: ErrorInterceptorService,
      multi: true
    },
    {
      provide: HAMMER_GESTURE_CONFIG,
      useClass: MyHammerConfig
  }
  ],
  bootstrap: [AppComponent]
})
export class AppModule {}
