import {Injectable} from '@angular/core';
import { SessionUserService } from '../session-user-service/session-user.service';


@Injectable({providedIn: 'root'})
export class RolesService {
  isUserBfk = false;
  isAdminBfk = false;
  isUserBwst = false;
  isAdminBwst = false;
  isObserverBwst = false;

  constructor(private sessionUser: SessionUserService) {}
  setRoles() {
    console.log('setRoles(), LoginService');
    const res = this
      .sessionUser
      .getUser();
    res
      .entitlements
      .forEach(element => {
        switch (element) {
          case 'UserBfk':
            this.isUserBfk = true;
            break;
          case 'AdminBfk':
            this.isAdminBfk = true;
            break;
          case 'UserBwst':
            this.isUserBwst = true;
            break;
          case 'AdminBwst':
            this.isAdminBwst = true;
            break;
          case 'ObserverBwst':
            this.isObserverBwst = true;
            break;

          default:
            break;
        }
      });
  }
}
