import {NgModule} from '@angular/core';
import {Routes, RouterModule} from '@angular/router';
import {LoginComponent} from './modules/login/components/login/login.component';
import {ForgotPwComponent} from './modules/forgot-pw/components/forgot-pw/forgot-pw.component';
import {AuthGuard} from './core/guards/auth.guard';
import { UserGuard } from './core/guards/user.guard';
import { NotFoundComponent } from './modules/not-found/components/not-found/not-found.component';
import { NavbarComponent } from './navbar/navbar.component';

const routes: Routes = [
  {
    path: '',
    component: LoginComponent
  }, {
    path: 'login',
    component: LoginComponent
  }, {
    path: 'forgotpassword',
    component: ForgotPwComponent
  }, {
    path: 'home',
    component: NavbarComponent,
    canActivate: [AuthGuard],
    children: [
      {
        path: 'appointments',
        loadChildren: 'src/app/modules/appointment/appointment.module#AppointmentModule'
      }, {
        path: '',
        loadChildren: 'src/app/modules/home/home.module#HomeModule'
      }, {
        path: 'marketplace',
        loadChildren: 'src/app/modules/marketplace/marketplace.module#MarketPlaceModule'
      }, {
        path: 'documents',
        loadChildren: 'src/app/modules/document/document.module#DocumentModule'
      }, {
        path: 'appointment',
        loadChildren: 'src/app/modules/add-update-appointment/add-update-appointment.module#AddUpdateAppointmentModule'
      }, {
        path: 'user-management',
        loadChildren: 'src/app/modules/user-managment/user-managment.module#UserManagementModule',
        canActivate: [UserGuard]
      }
    ]
  }, {
    path: '404',
    component: NotFoundComponent
  }, {
    path: '**',
    redirectTo: '/404'
  }
];

@NgModule({
  imports: [RouterModule.forRoot(routes)],


  exports: [RouterModule]
})
export class AppRoutingModule {}
