import {Injectable} from '@angular/core';
import {HttpClient} from '@angular/common/http';
import { SessionUserService } from '../session-user-service/session-user.service';
import { RolesService } from '../roles-service/roles.service';
import { map } from 'rxjs/internal/operators/map';
import { catchError } from 'rxjs/internal/operators/catchError';
import { of } from 'rxjs/internal/observable/of';
import { Observable } from 'rxjs';

const SERVER_URL = 'https://bfk-portal.herokuapp.com/api/authentication/';

@Injectable({providedIn: 'root'})
export class LoginService {
  public token: string;
  isProcessing = false;

  constructor(private sessionUser: SessionUserService, private http: HttpClient, private rolesService: RolesService) {}

  login(email: string, password: string): Observable < any > {
    console.log(JSON.stringify({email: email, password: password}));
    return this
      .http
      .post(SERVER_URL + 'login', {
        email: email,
        password: password
      })
      .pipe(map((response: any) => {
        console.log(response);
        const token = response.token;
        console.log(response);
        this.token = token;
        if (localStorage.getItem('checked') === 'true') {
          localStorage.setItem('currentUser', token);

        } else {
          sessionStorage.setItem('currentUser', token);
        }
        this
          .sessionUser
          .setUser(response.user);

        this
          .rolesService
          .setRoles();
        console.log('succesfull login');
        return true;
      }), catchError(err => {
        console.log(err);
        return of(false);
      }));
  }


}
