import { Component, OnInit, AfterViewInit, ChangeDetectorRef, ViewChild } from '@angular/core';
import {Router} from '@angular/router';
import {ToastService, SidenavComponent} from 'ng-uikit-pro-standard';
import {Location} from '@angular/common';
import * as _ from 'lodash';
import { User } from '../shared/models/user';
import { Offer } from '../shared/models/offer';
import { OrganisationService } from '../core/services/organisation-service/organisation.service';
import { AppointmentService } from '../core/services/appointment-service/appointment.service';
import { LogoutService } from '../core/services/logout-service/logout.service';
import { SessionUserService } from '../core/services/session-user-service/session-user.service';
import { NavbarButtonsService } from '../core/services/navbar-buttons-service/navbar-buttons.service';
import { RolesService } from '../core/services/roles-service/roles.service';
import { UserService } from '../core/services/user-service/user.service';
import { OfferService } from '../core/services/offer-service/offer.service';
import { Appointment } from '../shared/models/appointment';
import { MessagingService } from './../core/services/messaging-service/messaging.service';
import { FcmNotification } from '../shared/models/fcmNotification';

@Component({selector: 'app-navbar', templateUrl: './navbar.component.html', styleUrls: ['./navbar.component.scss']})
export class NavbarComponent implements OnInit, AfterViewInit {

  @ViewChild('sidenav')public sideNav: SidenavComponent;
  sessionUser: User = new User();
  users: Array < User > = [];
  sentOffers: Array < Offer > = [];
  receivedOffers: Array < Offer > = [];
  searchStr = '';

  selectedCalendar = false;
  selectedDocuments = false;
  selectedList = true;
  modalHeader = '';
  window = window.innerWidth;

  // tslint:disable-next-line:max-line-length
  constructor(private organisationService: OrganisationService,
      private appointmentService: AppointmentService,
      private logoutService: LogoutService,
      private router: Router,
      private sessionUserService: SessionUserService,
      public navbarButtons: NavbarButtonsService,
      public rolesService: RolesService,
      private toast: ToastService,
      private userService: UserService,
      private offerService: OfferService,
      public _location: Location,
      private changeDetector: ChangeDetectorRef,
      public messagingService: MessagingService) {

    this.sessionUser = this.sessionUserService.getUser();
  }

  ngOnInit() {
    this.messagingService.getMessages();
    this.rolesService.setRoles();
    this.navbarButtons.isListHistoryView = false;
    this.navbarButtons.isOfferView = false;
    this.fetchOrganisation();
    this.fetchAppointments();
    if (this.rolesService.isAdminBfk || this.rolesService.isAdminBwst) {
      this.fetchUser();
    }
    this.fetchOffers();
    this
      .appointmentService
      .appointmentToDelete
      .subscribe(appointment => {
        this.deleteAppointment(appointment);
      });
    this
      .appointmentService
      ._appointmentToAdd
      .subscribe(appointment => {
        this.addAppointment(appointment);
      });
    this
      .appointmentService
      .appointmentToUpdate
      .subscribe(appointment => {
        this.updateAppointment(appointment);
      });
    this
      .appointmentService
      ._shiftToMarket
      .subscribe(shiftToMarket => {
        this.sendShiftToMarket(shiftToMarket);
      });
    this
      .userService
      ._userToAdd
      .subscribe(user => {
        this.addUser(user);
      });
    this
      .userService
      ._userToUpdate
      .subscribe(user => {
        this.updateUser(user);
      });
    this
      .userService
      ._userToDelete
      .subscribe(user => {
        this.deleteUser(user);
      });
    this
      .offerService
      ._offerToAdd
      .subscribe(offer => {
        this.addOffer(offer);
      });
    this
      .appointmentService
      ._freeShiftToTake
      .subscribe(freeShiftId => {
        this.takeFreeShift(freeShiftId);
      });
    this
      .offerService
      ._replyToOffer
      .subscribe(data => {
        this.replyOffer(data);
      });

  }
  ngAfterViewInit(): void {
    this.messagingService.getPermission(this.sessionUser.id);
    this.messagingService.receiveMessage();
    this.changeDetector.detectChanges();
  }
  checkWidth(): boolean {
    return window.innerWidth <= 480 ? false : true;
  }
  getDropdownName() {
    return window.innerWidth <= 480 ? this.sessionUser.lastname : this.sessionUser.firstname + ' ' + this.sessionUser.lastname;
  }
  replyOffer(data: any): void {
    console.log(data);
    this
      .offerService
      .replyOffer(data.offerId, data.status)
      .subscribe(response => {
        if (response) {
          const offer = _.find(this.receivedOffers, ['id', data.offerId]);
          _.remove(this.receivedOffers, (val) => _.isEqual(val, offer));
          offer.offerStatus = data.status;
          this.receivedOffers = [
            ...this.receivedOffers,
            offer
          ];
          this.offerService._receivedOffers.next(this.receivedOffers);
          this.fetchAppointments();
        }
      });
  }
  takeFreeShift(freeShiftId: number): void {
    this
      .appointmentService
      .takeFreeShift(freeShiftId)
      .subscribe(data => {
        if (data) {
          this.fetchAppointments();
        } else {
          this
            .toast
            .error('Etwas ist schief gelaufen bitte nochmal probieren');
        }
      });
  }

  onLogout(): void {
    this
      .logoutService
      .logout();
  }
  fetchUser() {

    this
      .userService
      .getUsers()
      .subscribe(users => {
        console.log(users);
        this.users = [];
        this.users = users;
        this
          .userService
          ._allUsers
          .next(this.users);
      });
  }
  fetchOffers() {
    this
      .offerService
      .getOffers()
      .subscribe(offers => {
        this.sentOffers = [];
        this.receivedOffers = [];
        offers.forEach(offer => {
          if (offer.sender.email === this.sessionUser.email) {
            this
              .sentOffers
              .push(new Offer(offer.id, offer.status, offer.senderAppointment, offer.receiverAppointment, offer.sender, offer.receiver));
          } else if (offer.receiver.email === this.sessionUser.email) {
            this
              .receivedOffers
              .push(new Offer(offer.id, offer.status, offer.senderAppointment, offer.receiverAppointment, offer.sender, offer.receiver));
          }
          this
            .offerService
            ._receivedOffers
            .next(this.receivedOffers);
          this
            .offerService
            ._sentOffers
            .next(this.sentOffers);
        });
      });
  }
  fetchOrganisation() {
    this
      .organisationService
      .getOrganisations()
      .subscribe(success => {
      });
  }
  fetchAppointments() {
    this
      .appointmentService
      .getAppointments()
      .subscribe(success => {
      });
  }
  hideSideNav() {
    console.log(this.sideNav);
    if (window.innerWidth <= 1200) {
      this.sideNav.hide();
    }
  }
  changeToDocuments(): void {
    this.navbarButtons.changeToDocuments();
    this.hideSideNav();
    this
      .router
      .navigate(['/home/documents']);
  }
  changeToListView(): void {
    this.navbarButtons.changeToListView();
    this.hideSideNav();
    this
      .router
      .navigate(['/home/appointments/list']);
  }
  changeToMarketPlaceView(): void {
    this.navbarButtons.changeToMarketPlaceView();
    this.hideSideNav();
    this
      .router
      .navigate(['/home/marketplace'], {queryParams: {tabIndex: 1}});
  }
  changeToAddUserManagementView(): void {
    this.navbarButtons.changeToAddUserManagementView();
    this.hideSideNav();
    this
      .router
      .navigate(['/home/user-management/add']);
  }
  changeToListUserManagementView(): void {
    this.navbarButtons.changeToListUserManagementView();
    this.hideSideNav();
    this
      .router
      .navigate(['/home/user-management/list']);
  }
  changeToOverviewView(): void {
   this.navbarButtons.changeToOverviewView();
   this.hideSideNav();
    this
      .router
      .navigate(['/home']);
  }

  loadAddAppointmentComponent() {
    this
      .router
      .navigate(['home/appointment/add']);
  }
  addAppointment(appointment: Appointment) {
    this
      .appointmentService
      .addAppointment(appointment)
      .subscribe(response => {
        if (response.success) {
          this
            .toast
            .success(appointment.type + ' wurde angelegt');

            this.router.navigate(['home/appointments/details/' + response.id]);
        }
      });
  }
  updateAppointment(e: Appointment) {
    this
      .appointmentService
      .updateAppointment(e)
      .subscribe(data => {
        if (data === true) {
          this
            .toast
            .success(e.type + ' wurde erfolgreich geändert');

          this.router.navigate(['home/appointments/list']);
          this.fetchAppointments();
        }
      });
  }
  deleteAppointment(appointment: Appointment) {
    this
      .appointmentService
      .deleteAppointment(appointment)
      .subscribe(data => {
        if (data === true) {
          this.fetchAppointments();
          this.router.navigate(['home/appointments/list']);
        }
      });
  }
  addUser(user: User) {
    this
      .userService
      .addUser(user)
      .subscribe(data => {
        if (data.id) {
          user.id = data.id;
          this.users = [
            ...this.users,
            user
          ];
        }
      });
  }
  deleteUser(user: User) {
    this
      .userService
      .deleteUser(user)
      .subscribe(data => {
        if (data === true) {
          this.fetchUser();
        }
      });
  }
  sendShiftToMarket(shiftToMarket) {
    this
      .appointmentService
      .sendShiftToMarket(shiftToMarket.appointmentId)
      .subscribe(data => {
        if (data) {
          this.fetchAppointments();
        }
      });
  }
  updateUser(user: User) {
    this
      .userService
      .updateUser(user)
      .subscribe(data => {
        if (data === true) {
          this.fetchUser();
        }
      });
  }
  addOffer(offer: Offer): void {
    this
      .offerService
      .addOffer(offer)
      .subscribe(data => {
        if (data) {
          offer.id = data;
          this.sentOffers = [
            ...this.sentOffers,
            offer
          ];
          this
            .toast
            .success('Angebot gesendet');
            const fcm = new FcmNotification();
            fcm.body = 'Du hast ein neues Angebot erhalten';
            fcm.title = 'Neues Angebot';
            fcm.click_action = 'localhost:4200/home/marketplace?tabIndex=3';
            fcm.icon = './assets/favicon.ico';
            this.messagingService.getToken(offer.receiver.id).subscribe((token: string) => {
              if (token != null) {
                this.messagingService.sendNotification(token, fcm);
              }
            });
        }
      });
  }
  seeMessage(index: number, message: FcmNotification): void {
    console.log(index);
    console.log(message);
    this.messagingService.removeMessage(index);
    location.href = message.click_action;
  }

}
