import {Component, OnInit, OnDestroy} from '@angular/core';
import {Router} from '@angular/router';
import {FormGroup, FormBuilder, Validators} from '@angular/forms';
import { LoginService } from 'src/app/core/services/login-service/login.service';
import { RolesService } from 'src/app/core/services/roles-service/roles.service';
import { Subscription } from 'rxjs/internal/Subscription';

@Component({selector: 'app-login', templateUrl: './login.component.html', styleUrls: ['./login.component.scss']})
export class LoginComponent implements OnInit, OnDestroy {

  token: string;
  firefighter = false;
  intervalid: any;
  saveData = false;
  checkInput = true;
  loginForm: FormGroup;
  _loginSubscribtion: Subscription;

  // tslint:disable-next-line:max-line-length
  constructor(private router: Router,
    public loginService: LoginService,
    private rolesService: RolesService,
    private fb: FormBuilder) {
    this.loginForm = this
      .fb
      .group({
        'email': [
          '',
          [Validators.required, Validators.email]
        ],
        'password': [
          '',
          [Validators.required]
        ]
      }, { updateOn: 'submit'});
  }
  get password() { return this.loginForm.get('password'); }
  get email() { return this.loginForm.get('email'); }
  onSubmit() {
    localStorage.setItem('checked', this.saveData.toString());
    if (this.loginForm.valid) {
      this.loginService.isProcessing = true;
      this._loginSubscribtion = this
        .loginService
        .login(this.loginForm.get('email').value, this.loginForm.get('password').value)
        .subscribe(result => {
          console.log('Hello');
          console.log(this.router);
          if (result === true) {
            console.log(this.router);
            this
              .router
              .navigate(['home']);

          } else {
            const pw = this.loginForm.get('password').value;
            const email = this.loginForm.get('email').value;
            this.loginForm.reset();
            this.loginForm.patchValue({password: pw, email: email});
          }
        });
    }
  }

  ngOnInit() {

    if (localStorage.hasOwnProperty('currentUser')) {
      console.log(this.rolesService);
      this
        .rolesService
        .setRoles();
      this
        .router
        .navigate(['home']);

    }
  }
  isChecked(e): void {
    this.saveData = e.srcElement.checked;
  }
  ngOnDestroy(): void {
    if (this._loginSubscribtion != null) {
      this._loginSubscribtion.unsubscribe();
    }
  }

}
