import {Injectable} from '@angular/core';
import {HttpClient, HttpInterceptor, HttpRequest, HttpHandler} from '@angular/common/http';

@Injectable({providedIn: 'root'})
export class AuthInterceptorService implements HttpInterceptor {

  constructor() {}

  intercept(req: HttpRequest < any >, next: HttpHandler) {
    if (!localStorage.hasOwnProperty('currentUser') && !sessionStorage.hasOwnProperty('currentUser')) {
      return next.handle(req);
    }
    const authToken = localStorage.getItem('currentUser') || sessionStorage.getItem('currentUser');
    // Get the auth token from the service. Clone the request and replace the
    // original headers with cloned headers, updated with the authorization.
    const authReq = req.clone({
      headers: req
        .headers
        .set('Authorization', 'Bearer ' + authToken),
      responseType: 'json'

    });

    // send cloned request with header to the next handler.
    return next.handle(authReq);
  }
}
